.callback {
  @include screen(laptop) {
    padding-bottom: 100px;
  }
  @include screen(desktop) {
    padding-bottom: 120px;
  }
  &__wrapper {
    background-color: $blue-light;
    display: grid;
    gap: 20px;
    position: relative;
    @include screen(laptop) {
      gap: 40px;
      grid-template-columns: repeat(12, minmax(0, 1fr));
    }
  }
  &__content {
    padding: 40px 35px 0 35px;
    .subtitle {
      text-align: center;
      @include screen(laptop) {
        text-align: left;
      }
    }
    @include screen(laptop) {
      grid-column: span 5 / span 5;
      grid-column-start: 2;
      padding: 82px 0;
    }
  }
  &__image {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    bottom: -10px;
    @include screen(laptop) {
      bottom: 0;
      grid-column: span 5 / span 5;
      grid-column-start: 7;
    }
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      background-color: #D5EBFC;
      height: 142px;
      bottom: 0;
      width: 278px;
      transform: skew(-10deg, 0);
      @include screen(laptop) {
        height: 424px;
        width: 606px;
        bottom: -10px;
        right: -100px;
      }
      @include screen(desktop) {
        height: 500px;
        bottom: -10px;
        width: 800px;
        right: inherit;
      }
    }
    img {
      z-index: 2;
      max-width: 187px;
      position: relative;
      margin-right: -20px;
      max-height: 538px;
      @include screen(laptop) {
        position: absolute;
        max-width: 100%;
        bottom: -10px;
      }
    }
  }
  &__button {
    margin-top: 31px;
  }
}