.directions {
  background-color: $blue-primary;
  &__items {
    display: grid;
    gap: 4px;
    margin-top: 25px;
    @include screen(laptop) {
      margin-top: 40px;
      gap: 10px;
      grid-template-columns: repeat(3, minmax(0, 1fr));
    }
  }
  &__item {
    min-height: 135px;
    background-color: $blue-dark;
    display: flex;
    position: relative;
    align-items: center;
    padding-left: 100px;
    overflow: hidden;
    &:before {
      content: '';
      position: absolute;
      left: -121px;
      top: 65px;
      z-index: 1;
      transition: opacity $transition-slow;
      width: 532px;
      height: 532px;
      border-radius: 50%;
      background: rgba(#82C3FD, 0.2);
      filter: blur(40px);
      opacity: 0;
    }
    @include screen(laptop) {
      &:hover {
        &:before {
          opacity: 1;
        }
      }
    }
    @include screen(laptop) {
      padding-bottom: 50px;
      justify-content: center;
      align-items: flex-end;
      min-height: 542px;
      padding-left: 0;
    }
    @include screen(desktop) {
      min-height: 750px;

    }
    &.active {
      .directions__image {
        transform: scale(1);
      }
    }
  }
  &__image {
    z-index: 2;
    position: absolute;
    left: 0;
    transform: scale(0.9);
    transform-origin: left;
    transition: transform $transition-slow;
    @include screen(laptop) {
      top: 50px;
    }
    img {
      max-width: 100px;
      width: 100%;
      max-height: 120px;
      @include screen(laptop) {
        max-width: 100%;
        max-height: 364px;
      }
      @include screen(desktop) {
        max-width: 100%;
        max-height: 100%;
      }
    }
  }
  &__title {
    position: relative;
    z-index: 2;
    font-family: $stapel;
    text-transform: uppercase;
    color: $white-bright;
    @include headline-4;
    @include screen(laptop) {
      font-size: 32px;
      line-height: 38px;
    }
    @include screen(desktop) {
      @include headline-4;
    }
  }
}