@font-face {
  font-family: 'Stapel';
  src: url('../fonts/Stapel-Light.eot');
  src: local('Stapel Light'), local('Stapel-Light'),
      url('../fonts/Stapel-Light.eot?#iefix') format('embedded-opentype'),
      url('../fonts/Stapel-Light.woff2') format('woff2'),
      url('../fonts/Stapel-Light.woff') format('woff'),
      url('../fonts/Stapel-Light.ttf') format('truetype');
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: 'Mulish';
  src: local('Mulish Regular'), local('Mulish-Regular'),
      url('../fonts/Mulish-Regular.woff2') format('woff2'),
      url('../fonts/Mulish-Regular.woff') format('woff'),
      url('../fonts/Mulish-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}