@mixin screen($breakpoint) {
  @if $breakpoint == desktop {
    @media only screen and (min-width: $screen-desktop) {
      @content;
    }
  }

  @if $breakpoint == laptop {
    @media only screen and (min-width: $screen-laptop) {
      @content;
    }
  }

  @if $breakpoint == tablet {
    @media only screen and (min-width: $screen-tablet) {
      @content;
    }
  }

  @if $breakpoint == mobile {
    @media only screen and (min-width: $screen-mobile) {
      @content;
    }
  }
}

@mixin headline-1 {
  font-weight: 300;
  font-size: 32px;
  line-height: 38.4px;
  font-family: $stapel;
  @include screen(laptop) {
    font-size: 70px;
    line-height: 84px;
  }
  @include screen(desktop) {
    font-size: 120px;
    line-height: 132px;
  }
}

@mixin headline-2 {
  font-weight: 300;
  font-size: 26px;
  line-height: 31.6px;
  font-family: $stapel;
  @include screen(laptop) {
    font-size: 60px;
    line-height: 72px;
  }
  @include screen(desktop) {
    font-size: 80px;
    line-height: 96px;
  }
}

@mixin headline-3 {
  font-weight: 300;
  font-size: 22px;
  line-height: 26.4px;
  font-family: $stapel;
  @include screen(laptop) {
    font-size: 60px;
    line-height: 72px;
  }
}

@mixin headline-4 {
  font-weight: 300;
  font-size: 20px;
  line-height: 24px;
  font-family: $stapel;
  @include screen(laptop) {
    font-size: 45px;
    line-height: 54px;
  }
}

@mixin text-1 {
  font-weight: 300;
  font-size: 18px;
  line-height: 25.2px;
  font-family: $stapel;
  @include screen(laptop) {
    font-size: 34px;
    line-height: 40.8px;
  }
}

@mixin text-2 {
  font-size: 14px;
  line-height: 19.6px;
  font-family: $mulish;
  @include screen(laptop) {
    font-size: 20px;
    line-height: 25.1px;
  }
}

@mixin button {
  font-weight: 300;
  font-size: 16px;
  line-height: 120%;
  font-family: $stapel;
  @include screen(laptop) {
    font-size: 28px;
    line-height: 33.6px;
  }
}