.production {
  overflow: hidden;
  &__items {
    margin-top: 30px;
    @include screen(desktop) {
      margin-top: 152px;
    }
  }
  &__item {
    position: relative;
    display: grid;
    align-items: center;
    transition: opacity $transition-default, background-color $transition-default;
    padding: 27px 0;
    border-top: 1px solid $black-border;
    border-bottom: 1px solid $black-border;
    margin-top: -1px;
    gap: 20px;
    @include screen(laptop) {
      opacity: 0.25;
      padding: 40px 0;
      gap: 40px;
      grid-template-columns: repeat(12, minmax(0, 1fr));
      &:before {
        content: '';
        position: absolute;
        left: -999px;
        background-color: $blue-light;
        height: calc(100% + 2px);
        top: -1px;
        width: 999px;
        border-top: 1px solid $black-border;
        border-bottom: 1px solid $black-border;
        transition: opacity $transition-default;
        opacity: 0;
      }
      &:after {
        content: '';
        position: absolute;
        background-color: $blue-light;
        right: -999px;
        height: calc(100% + 2px);
        top: -1px;
        border-top: 1px solid $black-border;
        border-bottom: 1px solid $black-border;
        transition: opacity $transition-default;
        width: 999px;
        opacity: 0;
      }
    }
    @include screen(laptop) {
      &:hover {
        opacity: 1;
        background-color: $blue-light;
        .production__button {
          opacity: 1;
        }
        .production__image {
          opacity: 1;
        }
        &:before,
        &:after {
          opacity: 1;
        }
      }
    }
  }
  &__title {
    text-transform: uppercase;
    grid-column: span 5 / span 5;
    color: $black-dark;
    @include headline-4;
    @include screen(laptop) {
      font-size: 32px;
      font-family: $stapel;
      line-height: 38px;
    }
    @include screen(desktop) {
      @include headline-4;
    }
  }
  &__image {
    position: relative;
    z-index: 3;
    grid-column: span 2 / span 2;
    opacity: 0;
    transition: opacity $transition-default;
    display: none;
    img {
      transform: translateY(-50%);
      position: absolute;
    }
    @include screen(laptop) {
      display: block;
    }
  }
  &__content {
    display: grid;
    grid-column: span 5 / span 5;
    align-items: center;
    gap: 40px;
    @include screen(laptop) {
      grid-template-columns: repeat(5, minmax(0, 1fr));
    }
    p {
      font-size: 14px;
      line-height: 17.5px;
      color: $black-primary;
      grid-column: span 4 / span 4;
      @include screen(laptop) {
        font-size: 18px;
        line-height: 22px;
      }
      @include screen(desktop) {
        font-size: 24px;
        line-height: 30px;
      }
    }
  }
  &__button {
    position: absolute;
    width: 38px;
    height: 38px;
    top: 20px;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    transition: opacity $transition-default;
    border: 1px solid rgba($blue-dark, 0.6);
    svg {
      width: 14px;
    }
    @include screen(laptop) {
      position: relative;
      opacity: 0;
      width: 62px;
      height: 62px;
      top: auto;
      svg {
        width: auto;
      }
    }
  }
}