.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  padding: 8px 0 12px;
  transition: background-color $transition-default, border-color $transition-default;
  border-bottom: 1px solid transparent;
  @include screen(laptop) {
    padding: 24px 0 32px;
  }
  &.active {
    background-color: $blue-dark;
    border-color: $white-border;
  }
  &__logo {
    img {
      max-width: 117px;
      @include screen(laptop) {
        max-width: 100%;
      }
    }
  }
  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__nav {
    padding: 35px 0 0;
    top: 55px;
    position: fixed;
    width: 100%;
    height: calc(100dvh - 55px);
    overflow-y: auto;
    z-index: 100;
    align-items: flex-start;
    justify-content: space-between;
    flex-direction: column;
    left: 0;
    display: none;
    background-color: $blue-dark;
    &.active {
      display: flex;
    }
    @include screen(laptop) {
      padding: 0;
      flex-direction: row;
      position: static;
      align-items: center;
      gap: 30px;
      height: auto;
      width: auto;
      background-color: transparent;
      display: flex;
    }
  }
  &__list {
    width: 260px;
    background-color: $blue-dark;
    z-index: 2;
    display: none;
    padding: 25px 0 0 24px;
    align-items: flex-start;
    flex-direction: column;
    gap: 20px;
    @include screen(laptop) {
      padding: 23px 27px;
      top: 32px;
      position: absolute;
    }
    @include screen(laptop) {
      box-shadow: 0 0 4px $white-border;
    }
    &.active {
      display: flex;
    }
    &:before {
      content: '';
      position: absolute;
      display: none;
      top: -12px;
      height: 12px;
      left: 0;
      width: 100%;
      @include screen(laptop) {
        display: block;
      }
    }
  }
  &__links {
    display: flex;
    flex-direction: column;
    gap: 35px;
    @include screen(laptop) {
      flex-direction: row;
    }
  }
  &__sublink {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    @include screen(laptop) {
      &:hover {
        span {
          border-color: $white-bright;
        }
      }
    }
    span {
      text-transform: uppercase;
      color: $white-bright;
      font-family: $stapel;
      transition: border-color $transition-default;
      font-size: 24px;
      text-transform: uppercase;
      border-bottom: 1px solid transparent;
      line-height: 30px;
      padding-bottom: 2px;
      @include screen(laptop) {
        font-family: $mulish;
        font-size: 14px;
        line-height: 17.5px;
      }
    }
    svg {
      display: none;
      @include screen(laptop) {
        display: block;
      }
    }
  }
  &__wrap {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  &__contacts {
    margin-top: 35px;
    background-color: #013462;
    width: 100vw;
    left: 0;
    position: relative;
    padding-bottom: 52px;
    &__wrap {
      display: flex;
      flex-direction: column;
    }
    @include screen(laptop) {
      display: none;
    }
  }
  &__link {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
    @include screen(laptop) {
      gap: 10px;
    }
    a {
      font-size: 14px;
      line-height: 19px;
      text-transform: uppercase;
      color: $white-bright;
    }
    &-big {
      margin-top: 35px;
      span {
        font-size: 14px;
        font-family: $stapel;
        line-height: 16.8px;
        text-transform: uppercase;
        color: $white-primary;
      }
      a {
        font-family: $stapel;
        font-size: 26px;
        line-height: 31px;
        @include screen(laptop) {
          font-size: 36px;
          line-height: 43px;
        }
        @include screen(desktop) {
          font-size: 50px;
          line-height: 60px;
        }
      }
    }
    @include screen(laptop) {
      display: flex;
      align-items: center;
      &:hover {
        .header__list {
          display: flex;
        }
      }
    }
    a {
      font-family: $stapel;
      color: $white-bright;
      font-size: 24px;
      text-transform: uppercase;
      line-height: 30px;
      @include screen(laptop) {
        font-family: $mulish;
        font-size: 14px;
        line-height: 17.5px;
      }
    }
    svg {
      width: 20px;
      height: 9px;
      @include screen(laptop) {
        width: auto;
        height: auto;
      }
    }
  }
  &__trigger {
    width: 20px;
    height: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    svg {
      margin-bottom: 4px;
      transition: transform $transition-default;
      @include screen(laptop) {
        margin-bottom: 0;
      }
    }
    &.active {
      svg {
        transform: rotate(180deg);
      }
    }
  }
  &__burger {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 35px;
    height: 35px;
    svg {
      width: 22px;
      height: 22px;
      path,
      rect {
        transition: transform $transition-default, opacity $transition-default;
        fill: $white;
      }
    }
    &.active {
      svg {
        rect {
          &:nth-child(1) {
            transform: translateX(14px) translateY(-3px) rotate(45deg);
          }
          &:nth-child(2) {
            transform: translateX(-7px) translateY(17px) rotate(-45deg);
          }
          &:nth-child(3) {
            opacity: 0;
          }
        }
      }
    }
    @include screen(laptop) {
      display: none;
    }
  }
}