.footer {
  background-color: $blue-dark;
  position: fixed;
  bottom: 0;
  width: 100%;
  padding: 40px 0 0;
  @include screen(laptop) {
    padding: 70px 0 0;
  }
  &__logo {
    @include screen(laptop) {
      grid-column: span 2 / span 2;
    }
  }
  &__wrapper {
    padding-bottom: 40px;
    display: grid;
    @include screen(laptop) {
      padding-bottom: 67px;
      grid-template-columns: repeat(12, minmax(0, 1fr));
    }
  }
  &__copy {
    border-top: 1px solid $white-border;
    display: flex;
    padding: 20px 0 35px;
    justify-content: space-between;
    flex-direction: column-reverse;
    gap: 15px;
    @include screen(laptop) {
      align-items: center;
      padding: 35px 0;
      flex-direction: row;
    }
    p {
      font-size: 14px;
      line-height: 19px;
      color: $white-primary;
    }
  }
  &__contacts {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    margin-top: 50px;
    gap: 40px;
    @include screen(laptop) {
      margin-top: 0;
      padding-left: 40px;
      gap: 20px;
      grid-column: span 6 / span 6;
    }
  }
  &__menu {
    margin-top: 50px;
    display: flex;
    gap: 50px;
    @include screen(laptop) {
      padding-left: 40px;
      margin-top: 0;
      grid-column: span 4 / span 4;
      gap: 124px;
    }
  }
  &__col {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    gap: 20px;
  }
  &__link {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
    @include screen(laptop) {
      gap: 10px;
    }
    a {
      font-size: 14px;
      line-height: 19px;
      text-transform: uppercase;
      color: $white-bright;
    }
    &-big {
      span {
        font-size: 14px;
        font-family: $stapel;
        line-height: 16.8px;
        text-transform: uppercase;
        color: $white-primary;
      }
      a {
        font-family: $stapel;
        font-size: 26px;
        line-height: 31px;
        @include screen(laptop) {
          font-size: 36px;
          line-height: 43px;
        }
        @include screen(desktop) {
          font-size: 50px;
          line-height: 60px;
        }
      }
    }
  }
  &__policy {
    font-size: 14px;
    line-height: 19px;
    color: $white-primary;
  }
}