.brands {
  position: relative;
  background-color: $blue-dark;
  padding-bottom: 135px;
  &__slider {
    margin-top: 25px;
    @include screen(desktop) {
      margin-top: 40px;
    }
    &.swiper {
      padding-bottom: 25px;
      .swiper-pagination {
        bottom: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        top: auto;
        padding: 2px 0;
        bottom: 0;
        &-bullet {
          background-color: $white-primary;
          opacity: 1;
          width: 6px;
          height: 6px;
          border-radius: 50%;
          transition: transform $transition-default;
          &-active {
            background-color: $white-bright;
            transform: scale(1.5);
          }
        }
      }
    }
  }
  &__item {
    display: flex;
    padding: 35px;
    min-height: 332px;
    flex-direction: column;
    justify-content: space-between;
    background-color: #0D4E88;
    transition: background-color $transition-slow;
    @include screen(laptop) {
      padding: 32px;
      min-height: 400px;
      &:hover {
        background-color: #256094;
      }
    }
    @include screen(desktop) {
      padding: 44px 55px;
      min-height: 561px;
    }
  }
  &__icon {
    margin: 0 auto;
    img {
      max-height: 65px;
      max-width: 156px;
      @include screen(desktop) {
        max-height: auto;
      }
    }
  }
  &__title {
    padding-top: 20px;
    color: $white-bright;
    position: relative;
    font-size: 16px;
    line-height: 20px;
    @include screen(desktop) {
      @include text-2;
    }
    &:before {
      content: '';
      left: 0;
      position: absolute;
      top: 0;
      width: 0;
      height: 1px;
      background-color: $white-border;
      transition: width $transition-slow;
    }
    &.active {
      &:before {
        width: 100%;
      }
    }
  }
  &__button {
    margin-top: 50px;
    position: absolute;
    bottom: 45px;
    left: 50%;
    transform: translate(-50%);
    @include screen(desktop) {
      position: static;
      margin-top: auto;
      transform: translate(0);
      bottom: auto;
    }
  }
}