.hero {
  padding: 0 0 60px;
  background-color: $blue-dark;
  position: relative;
  @include screen(laptop) {
    padding: 0;
    height: 100dvh;
  }
  &__bg {
    position: absolute;
    top: 0;
    height: calc(100% + 60px);
    width: 100%;
    left: 0;
    &:before {
      content: '';
      position: absolute;
      left: 50%;
      transform: translate(-50%, -50%);
      top: 70%;
      width: 700px;
      height: 700px;
      border-radius: 50%;
      background: rgba(#82C3FD, 0.2);
      filter: blur(40px);
      display: none;
      @include screen(laptop) {
        display: block;
      }
    }
    &:after {
      content: '';
      position: absolute;
      left: 70%;
      transform: translate(-50%, -50%);
      top: 40%;
      width: 700px;
      height: 700px;
      border-radius: 50%;
      background: rgba(#82C3FD, 0.2);
      filter: blur(40px);
      display: none;
      @include screen(laptop) {
        display: block;
      }
    }
    img {
      height: calc(100% + 60px);
      width: 100%;
      object-fit: cover;
    }
    @include screen(laptop) {
      display: block;
    }
    &-pc {
      display: none;
      @include screen(laptop) {
        display: block;
      }
    }
    &-mob {
      @include screen(laptop) {
        display: none;
      }
    }
  }
  &__slide {
    display: flex;
    flex-direction: column;
    align-items: center;
    @include screen(laptop) {
      height: 100dvh;
      display: grid;
      grid-template-columns: repeat(12, minmax(0, 1fr));
    }
  }
  &__wrapper {
    position: relative;
    &.active {
      .hero__text {
        &:before {
          width: 100%;
        }
      }
      .hero__image {
        @include screen(laptop) {
          transform: scale(1.1);
        }
      }
    }
    .swiper {
      z-index: 3;
      position: relative;
      padding-bottom: 25px;
      .swiper-pagination {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 2px 0;
        bottom: 0;
        @include screen(laptop) {
          bottom: 57px;
        }
        &-bullet {
          background-color: $white-primary;
          opacity: 1;
          width: 6px;
          height: 6px;
          border-radius: 50%;
          transition: transform $transition-default;
          &-active {
            background-color: $white-bright;
            transform: scale(1.5);
          }
        }
      }
    }
  }
  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    padding: 87px 0 23px;
    @include screen(laptop) {
      align-items: flex-start;
      padding: 173px 0;
      justify-content: space-between;
      grid-column: span 6 / span 6;
    }
    @include screen(desktop) {
      padding: 254px 0 180px;
    }
  }
  &__title {
    color: $white-bright;
    text-align: center;
    @include headline-1;
    @include screen(laptop) {
      text-align: left;
    }
  }
  &__text {
    padding-top: 25px;
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    color: $white-primary;
    max-width: 518px;
    width: 100%;
    position: relative;
    @include screen(laptop) {
      font-size: 18px;
      line-height: 22px;
      margin-top: 55px;
      text-align: left;
    }
    @include screen(desktop) {
      font-size: 24px;
      line-height: 30px;
      margin-top: 80px;
    }
    &:before {
      content: '';
      position: absolute;
      top: 0;
      background-color: $white-border;
      transition: width $transition-slow;
      width: 0;
      left: 0;
      height: 1px;
    }
  }
  &__image {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    grid-column: span 6 / span 6;
    transition: transform $transition-slow;
    @include screen(desktop) {
      margin-left: -80px;
    }
    img {
      max-height: 204px;

      @include screen(laptop) {
        max-height: 463px;
      }
      @include screen(desktop) {
        max-height: 100%;
      }
    }
  }
}