.about {
  background-color: $blue-light;
  &__items {
    display: grid;
    margin-top: 40px;
    row-gap: 40px;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    column-gap: 35px;
    @include screen(laptop) {
      margin-top: 130px;
      gap: 60px;
      grid-template-columns: repeat(4, minmax(0, 1fr));
    }
    @include screen(desktop) {
      gap: 80px;
      margin-top: 160px;
    }
  }
  &__item {
    display: flex;
    padding-top: 20px;
    align-items: flex-start;
    gap: 3px;
    flex-direction: column;
    grid-column: span 1 / span 1;
    position: relative;
    @include screen(desktop) {
      gap: 10px;
    }
    &:before {
      content: '';
      height: 1px;
      position: absolute;
      width: 0;
      left: 0;
      top: 0;
      background-color: #AFBFCB;
      transition: width $transition-slow;
    }
    &.active {
      &:before {
        width: 100%;
      }
    }
  }
  &__title {
    font-family: $stapel;
    font-size: 40px;
    line-height: 48px;
    @include screen(laptop) {
      font-size: 70px;
      line-height: 84px;
    }
    @include screen(desktop) {
      font-size: 100px;
      line-height: 120px;
    }
  }
  &__text {
    font-size: 12px;
    line-height: 18px;
    color: $black-primary;
    @include screen(laptop) {
      font-size: 18px;
      line-height: 22px;
    }
    @include screen(desktop) {
      font-size: 24px;
      line-height: 30px;
    }
  }
}